.TransactionIframe .purchaseButtons {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-content: flex-start;
  justify-content: space-around;
}

.TransactionIframe .continueButton {
  border-radius: 52px;
  align-items: center;
  text-align: center;
  font-feature-settings: 'liga' off;
  color: #FFFFFF;
  background-color: #F28F17;
  margin-left: auto;
  margin-right: auto;
  box-shadow: none !important;
  display: inline-block;
  width: 218px;
  margin-bottom: 15px !important;
  height: 48px;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
}

.TransactionIframe .continueButton.creditCard {
  background-color: #F28F17;
}


.TransactionIframe .continueButton.bit {
  background: linear-gradient(180deg, #2C5584 0%, #39B3B7 100%);
}

.TransactionIframe .continueButton.bit img {
  margin-right: 10px;
}

.TransactionIframe .paypalContainer {
  margin-top: 20px;
  background-color: #fbe49052;
  border-radius: 25px;
  padding-top: 30px;
}

.TransactionIframe .code {
  border-color: black;
  border-style: double;
  width: fit-content;
  width: -moz-max-content;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  padding: 15px;
  border-radius: 20px;
  margin-top: 20px;
  margin-bottom: 25px;
  background-color: transparent;
  font-size: 45px;
  font-weight: 700;
}

@media (max-width: 500px) {
  .TransactionIframe .purchaseButtons {
    flex-direction: column;
    align-content: center;
    justify-content: center;
  }
}
