form.PhoneForm button.continueButton {
  border-radius: 52px;
  font-weight: 700;
  font-size: 25px;
  line-height: 24px;
  align-items: center;
  text-align: center;
  font-feature-settings: 'liga' off;
  color: #FFFFFF;
  padding: 10px;
  background-color: #F28F17;
  margin-left: auto;
  margin-right: auto;
  display: block;
  width: fit-content;
  box-shadow: none !important;
}


@media (max-width: 351px) {
  form.PhoneForm button.continueButton {
    font-size: 6vw;
  }

}
